<template>
  <v-layout>
    <v-dialog v-model="dialog" persistent max-width="45%">
      <v-card>
        <v-form>
          <v-card-title class="headline">Para continuar debes registrarte</v-card-title>
          <v-card-text>
            <v-container>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field label="Nombre" required v-model="name"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    label="Teléfono"
                    type="tel"
                    v-model="phone"
                    :rules="[rules.required, rules.number]"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    label="Correo electrónico"
                    type="email"
                    :rules="[rules.required, rules.email]"
                    v-model="email"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat @click="closeDialog">Cancelar</v-btn>
            <v-btn color="green darken-1" flat @click="signUp">Enviar</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import Api from "@/services/api.js"
import { mapState } from "vuex";
export default {
  props: ["dialog", "status", "banco", "creditoPreautorizado"],
  data() {
    return {
      name: "",
      email: "",
      phone: "",

      dialog: this.dialog,
      rules: {
        required: value => !!value || "Requerido.",
        email: value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Correo no valido.";
        },
        number: value => {
          const pattern = /^([0-9])*$/;
          return pattern.test(value) || "Teléfono invalido.";
        }
      }
    };
  },
  methods: {
    signUp() {
      let data = {
        name: this.name,
        email: this.email,
        phone: this.phone,
        status: this.status,
        type: 0,
        date: new Date(),

        monthlyPayment: this.banco.valores.mensualidad,
        earnings: this.banco.valores.ingresos,
        mortgageRate: this.banco.valores.tasa,
        requestedCredit: this.banco.valores.montoDeCredito,
        preAuthorizedCredit: this.creditoPreautorizado,
        bank: this.banco.nombre
      };
      

      Api.post("transact/create.php", data).then(response => {
        console.log(response.data);
      });
    },
    closeDialog() {
      this.$emit("closeDialog");
    }
  },
  computed: {
    ...mapState([
      "tipoDeIngresoSeleccionado",
      "ingresoMensual",
      "bancos",
      "backend"
    ])
  }
};
</script>